import React from "react";
import API from "../../utils/index";

function AboutMe() {
  return (
    <div className="row center" id="aboutRow">
      <div id="cardRow">
        <div className="center" style={{ marginTop: "30px" }}>
          <h4>ABOUT ME</h4>
          <hr style={{ width: "5%" }}></hr>
        </div>
        <div className="image" id="about">
          <img
            src="images/profileimg.jpeg"
            style={{
              height: "210px",
              width: "230px",
              borderRadius: "50%",
              marginTop: "10px"
            }}
          />
        </div>
        <div className="l4 m6 s12" style={{ marginTop: "40px" }}>
          <p>
            My name is Sachin Jhaveri and I am a{" "}
            <span style={{ color: "orange" }}>FULL STACK DEVELOPER</span> in
            Toronto.
          </p>
          <p className="center">
            &lsquo; I'm passionate about learning new skills and technology. I
            am a person who is focused <br></br> more towards providing a
            practical solution to a given business/technical problem and{" "}
            <br></br>always looking forward to all the new opportunities that
            come along the way.&rsquo;
          </p>
        </div>
        <div style={{ marginTop: "25px" }}>
          {/* <form method="get" action="file.doc"> */}
          {/* <a href="public/Sachin_Jhaveri_Resume.docx" download="Resume.docx"> */}
          <button
            type="submit"
            onClick={() => {
              API.getResume();
            }}
            target="_blank"
            id="resumebtn"
          >
            RESUME
          </button>
          {/* </a> */}
          {/* </form> */}
        </div>
      </div>
    </div>
  );
}

export default AboutMe;
